.circleLogo {
  background: rgba(0, 0, 0, 0);
  width: 60px;
  height: 60px;
  margin: 3% auto 2.5% 10%;
}
.Nav_item_wrapper {
  display: flex;
  flex-flow: row;
  margin-right: auto;
  padding-left: 20%;
  list-style: none;
}
.Nav_link {
  color: var(--gold);
  list-style: none;
  background-color: none;
  font-size: 16px;
  font-weight: bold;
  letter-spacing: 2px;
  font-family: "Open Sans";
}
.Nav_item {
  margin-left: 10%;
  margin-right: 0;
  background-color: none;
}
.Header_root {
  width: 100%;
}
.Header {
  display: flex;
  flex-flow: row;
  height: 80px;
  align-items: center;
  justify-content: left;
  background-color: rgba(0, 0, 0, 0.4);
  width: 100vw;
}
.NavBar {
  max-width: 100vw;
  display: flex;
}
@media screen and (max-width: 1050px) {
  .Nav_item {
    margin-left: 5%;
  }
}
@media screen and (max-width: 800px) {
  .Nav_item {
    margin-left: 3%;
  }
}
@media screen and (max-width: 600px) {
  .NavBar {
    display: flex;
    flex-flow: column;
    flex-wrap: wrap;
  }
  .Header_root {
    display: flex;
    flex-flow: column;
  }
  .Nav_item_wrapper {
    flex-wrap: wrap;
    display: flex;
    width: 100%;
    margin-left: -5%;
  }
}

@media screen and (max-width: 450px) {
  .NavBar {
    max-width: 98vw;
  }
  .Nav_item_wrapper {
    margin-bottom: 0;
  }
}
