  --default-font: "Open Sans", sans-serif;

/*Icons/Images*/
  --next-section-arrow: url(data:image/png;base64,);
  --code-background: url(../../static/media/background.1bd3062e.png);
  --email-logo: url(data:image/png;base64,);
